import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { padding } from 'utils/styles';
const { mediaQueries, screenSizes } = utils;

export const noResults = () => ({
    textAlign: 'center',
    paddingTop: 168,
    paddingBottom: 216,
    [`${mediaQueries.phablet}`]: {
        padding: 152,
    },
});

export const selectWrapper = () => ({
    flex: '1 0 auto',
    alignSelf: 'auto',
    [`${mediaQueries.normal}`]: {
        '&.select-wrapper': {
            marginRight: 15,
            alignSelf: 'center',
            '&:last-of-type': {
                marginRight: 0,
            },
        },
    },
});

export const filtersWrapper = () => ({
    padding: `0 32px ${padding.semi}px 32px`,
    [`${mediaQueries.phablet}`]: {
        padding: `0 0 ${padding.semi}px 0`,
    },
    ' .select-filter-container': {
        [mediaQueries.ie11Phablet]: {
            display: 'block',
        },
        [mediaQueries.ie11normal]: {
            display: 'flex',
        },
    },
});

export const loadMoreButton = (isMobile = false) => ({
    textAlign: 'center',
    marginBottom: isMobile ? null : -utils.padding.semi,
    paddingTop: 40,
    paddingBottom: isMobile ? 48 : 0,
});



