import * as React from 'react';
import * as glamorous from 'glamorous';
import * as Router from 'react-router';
import { autobind } from 'core-decorators';
import 'what-input';

const { Div, Ul, Li } = glamorous;
const { Link } = Router;

import * as styles from './TabArray-styles';
import * as dropdownStyles from './DropdownTab-styles';

export interface IDropdownTabProps {
  css?: { [key: string]: any };
  analyticsEvent?: Function;
  onSelect: () => void;
  selected?: boolean;
  selectedColor?: string;
  items?: Array<{
    label: string;
    link: string;
  }>;
  uri?: string;
}

export interface IDropdownTabState {
  isActive: boolean;
}

export class DropdownTab extends React.Component<IDropdownTabProps, IDropdownTabState> {

  state = {
    isActive: false,
  };

  isTouchEnabled = (typeof window !== 'undefined')  ? ('ontouchstart' in window) : false;
  closeTimeout = null;
  el: any;

  @autobind
  handleMouseOver() {
    this.setActive();
  }

  @autobind
  handleMouseLeave() {
    this.closeTimeout = setTimeout(this.closeNav, 100);
  }

  @autobind
  setActive() {
    clearTimeout(this.closeTimeout);
    if (!this.state.isActive) {
      this.setState({ isActive: true });
    }
  }

  @autobind
  closeNav() {
    if (this.state.isActive) {
      this.setState({ isActive: false });
    }
  }

  @autobind
  handleClick(e, label?: string, link?: string) {
    if (this.state.isActive) {
      this.setState({ isActive: false });
      if (this.props.analyticsEvent) {
        this.props.analyticsEvent({
          action: 'click',
          label: `subNav:button-${ link }-${ label }`,
          category: `subNav:button-${ link }-${ label }`,
        });
      }
    }
  }

  @autobind
  handleTabKeydown(e) {
    if (e.keyCode === 32) { // spacebar
      if (!this.state.isActive) {
        this.setActive();
      } else {
        this.closeNav();
      }
    }

    if (e.keyCode === 40) {
      this.setActive();
    }

    if (e.keyCode === 38) {
      this.closeNav();
    }
  }

  renderSubNav() {
    return (
      <Ul
        role="menu"
        className={'subnav'}
        css={dropdownStyles.subNavWrapper(this.state.isActive)}
        onMouseOver={this.setActive}
        onMouseLeave={this.closeNav}
        aria-expanded={this.state.isActive ? 'true' : 'false'}
      >
        {this.renderItems()}
      </Ul>
    );
  }

  renderItems() {
    return this.props.items.map((item, index) => {
      const isExternalLink = item.link ? item.link.indexOf('http') : -1;
      return (
        <Li
          role="menuitem"
          key={item.label}
          css={dropdownStyles.subNavItem()}
        >
          {isExternalLink !== -1 ?
            <a
              key={index}
              href={item.link}
              onClick={(evt) => this.handleClick(evt, item.label, item.link)}
              target="_blank"
            >
              {item.label}
            </a>
            :
            <Link
              key={index}
              to={item.link}
              onClick={(evt) => this.handleClick(evt, item.label, item.link)}
            >
              {item.label}
            </Link>
          }
        </Li>
      );
    });
  }

  render() {
    const { selected, children, selectedColor, css } = this.props;
    return (
      <Li
        aria-haspopup="true"
        css={[
          dropdownStyles.triggerWrapper(),
          styles.tabStyles({ selected, selectedColor }),
          dropdownStyles.dropdownTabStyles(this.state.isActive),
          css,
        ]}
        style={{height: '100%'}}
        onMouseOver={!this.isTouchEnabled && this.handleMouseOver}
        onMouseLeave={!this.isTouchEnabled && this.handleMouseLeave}
      >
        <Link
          className={'nav-tab-link'}
          to={this.props.uri}
          onKeyDown={this.handleTabKeydown}
          onClick={(e) => {
            e.preventDefault();
            this.props.onSelect();
          }}
        >
          {children}
        </Link>
        {this.renderSubNav()}
      </Li>
    );
  }
}



