import * as React from 'react';
import * as glamorous from 'glamorous';
import * as ReactDOM from 'react-dom';
import { autobind } from 'core-decorators';

import { setFontLoaded } from '../../../../../../state/actionCreators/appCreator';
import { store } from '../../../../../../state/createStore';

const { Div, Span } = glamorous;

import { screenSizes } from 'utils/styles';
import { spanBottomMargin } from '../../base/GridCard-base-styles';
import { contentContainer, contentSpanWrapper} from '../../base/GridCard-base-styles';

export interface IContentContainerProps {
  children?: React.ReactNode;
  iterateContent?: Function;
  content: string;
  windowWidth?: number;
}

export interface IContentContainerState {
  breakList: Array<number>;
  isLoaded: boolean;
}

export class ContentContainerImpl extends React.Component<IContentContainerProps, IContentContainerState> {

  constructor(props) {
    super(props);

    this.content = props.content;
    // repalace normal hypens with non-linebreaking hypens
    if (props.content.indexOf('-') > -1) {
      this.content = props.content.replace(/-/g, '\u2011');
    }
  }

  state = {
    breakList: [],
    isLoaded: false,
  };

  el: any;
  elHeight: number = 0;
  prevHeight: number = 0;
  breakList = [];
  spanContainer: any;
  content: string;

  init() {
    if (this.el) {
      this.breakList = [];
      this.iterateContent();
    }
  }

  componentDidMount() {
    // If smaller than `normal` then no need to break lines
    if (window.matchMedia(`(max-width:${screenSizes.normal}px)`).matches) {
      this.setState({ isLoaded: true });
      return;
    }

    setTimeout(() => {
      this.elHeight = this.el.getBoundingClientRect().height;
      requestAnimationFrame(this.checkElHeight); // Forcing timeout to wait for font to load. Revisit to refactor.
    }, 300);
  }

  // Runs a check to ensure that the font is loaded.
  @autobind
  checkElHeight() {
    if (store.getState().app.isFontLoaded) {
      this.init();
      return;
    }
    if (this.el && this.el.getBoundingClientRect().height !== this.elHeight) { // This block is never reached
      // store.dispatch(setFontLoaded(true));
      // this.init();
    } else {
      store.dispatch(setFontLoaded(true));
      requestAnimationFrame(this.checkElHeight);
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.windowWidth !== this.props.windowWidth) {
      if (window.matchMedia(`(max-width:${screenSizes.normal}px)`).matches) {
        this.setState({ breakList: [] });
        return;
      }
      this.setState({ breakList: [] }, this.init);
    }
  }

  iterateContent() {
    const _tempContainer = document.createElement('div');
    this.spanContainer.appendChild(_tempContainer);
    const contentArray = this.content.split(' ');
    contentArray.forEach((text, index, stringArray) => {
      const textNode = document.createTextNode(`${text} `);
      _tempContainer.appendChild(textNode);
      const height = _tempContainer.getBoundingClientRect().height;
      if (this.prevHeight !== 0 && height !== this.prevHeight) {
        this.breakList.push(index - 1);
      }
      this.prevHeight = height;
      if (index === stringArray.length - 1) {
        this.spanContainer.removeChild(_tempContainer);
        this.setState({ breakList: this.breakList, isLoaded: true });
      }
    });
  }

  setSpanContainerRef = (node) => {
    if (node) {
      this.spanContainer = node;
    }
  }

  setHtml (content) {

    const spanElements = [];
    const contentArray = content.split(' ');

    this.state.breakList.forEach((postion, index) => {

      let span;

      if (index === 0) {
        span = contentArray.slice(0, postion + 1);
      } else {
        span = contentArray.slice(this.state.breakList[index - 1] + 1, postion + 1);
      }

      spanElements.push(span.join(' '));
    });

    if (this.state.breakList.length) {
      const remainderArray = contentArray.slice(this.state.breakList[this.state.breakList.length - 1] + 1, contentArray.length);

      spanElements.push(remainderArray.join(' '));
    }

    return (
      <Div
        innerRef={(el) => this.el = el}
        css={contentContainer(this.state.isLoaded)}
      >
        {this.state.breakList.length ? (
          spanElements.map((spanContent, i) => {
            return (
              <Span key={i} className={'content-span-wrapper'} css={contentSpanWrapper} innerRef={this.setSpanContainerRef}>
                { i === 0 ? this.props.children : null }
                <span className={'content-span'}>
                  {spanContent}
                </span>
              </Span>
            );
          })
        ) : (
            <Span className={'content-span-wrapper'} css={contentSpanWrapper} innerRef={this.setSpanContainerRef}>
            {this.props.children}
            <span className={'content-span'}>
              {content}
            </span>
          </Span>
        )}
      </Div>
    );

  }

  render() {
    return (
      this.setHtml(this.content)
    );
  }
}

export const ContentContainer = ContentContainerImpl;



