import * as React from 'react';
import * as glamorous from 'glamorous';
import {Link} from 'react-router';
import 'what-input';

const { Li } = glamorous;

import * as styles from './TabArray-styles';

export type NavTabProps = {
  css?: { [key: string]: any };
  onSelect?: () => void;
  selected?: boolean;
  selectedColor?: string;
  uri: string;
  nav?: boolean;
};

export const NavTab: React.SFC<NavTabProps> =
  ({ selected, children, onSelect, selectedColor, css, uri, nav}) => {
    return (
      <Li
        css={[styles.tabStyles({ selected, selectedColor }), css]}
        role={!nav ? 'tab' : null}
      >
        <Link
          className={'nav-tab-link'}
          to={uri}
          onClick={(e) => {
            e.preventDefault();
            onSelect();
          }}
          onKeyDown={(e) => {
            if (e.keyCode === 13 || e.keyCode === 32) {
              e.preventDefault();
              onSelect();
            }
          }}
        >
          {children}
        </Link>
      </Li>
    );
  };

NavTab.displayName = 'NavTab';



