import * as React from 'react';
import * as glamorous from 'glamorous';
import * as styles from './SidePagination-styles';

const {
  Li,
  Div,
} = glamorous;

interface IPaginationItemProps {
  title: string;
  index: number;
  sectionRef: any;
  yOffset: number;
  handleClick?: Function;
  active?: boolean;
  primaryColor: string;
  secondaryColor: string;
  navRef?: any;
}

interface IPaginationItemState {
  titleWidth: number;
  isTouchEnabled: boolean;
}

export class SidePaginationItem extends React.Component<IPaginationItemProps, IPaginationItemState> {
  state = {
    titleWidth: 0,
    isTouchEnabled: (typeof window !== 'undefined') ? ('ontouchstart' in window) : false,
  };

  itemTitle: any;

  componentDidMount() {
    this.refReady(this.itemTitle);
  }

  refReady(ref) {
    this.setState({titleWidth: ref.offsetWidth});
  }

  render() {
    const {
      title,
      index,
      handleClick,
      active,
      primaryColor,
      secondaryColor,
      sectionRef,
      yOffset,
    } = this.props;

    return (
      <Li
        className={`${active && 'active'}`}
        onClick={ () => handleClick(sectionRef, yOffset, title, index) }
        css={ styles.listItemContainer(primaryColor, this.state.titleWidth, this.state.isTouchEnabled) }
      >
        <Div css={ styles.dotHolder }>
          <Div className="dot" />
        </Div>
        <Div className={`list-item ${active && 'active'}`}
             css={ styles.listItem(secondaryColor, this.state.titleWidth, this.state.isTouchEnabled) }>
          <Div innerRef={(ref) => { this.itemTitle = ref; }}
               className="item-title"
               css={ styles.listItemTitle }>
               {title}
          </Div>
        </Div>
      </Li>
    );
  }
}



