import * as React from 'react';
import * as glamorous from 'glamorous';
import * as glamor from 'glamor';
import 'what-input';

const { Ul, Nav } = glamorous;

import * as styles from './TabArray-styles';

export type TabArrayProps = {
  css?: { [key: string]: any };
  nav?: boolean;
};

export class TabArray extends React.Component<TabArrayProps, void> {

  generateTabArray() {
    let array;
    if (this.props.nav) {
      array = (
        <Nav>
          <Ul
            css={[styles.tabListStyles(), this.props.css]}
          >
            {this.props.children}
          </Ul>
        </Nav>
      );
    } else {
      array = (
        <Ul
          css={[styles.tabListStyles(), this.props.css]}
          role="tablist"
        >
          {this.props.children}
        </Ul>
      );
    }
    return array;
  }

  render() {
    return (
      this.generateTabArray()
    );
  }
}



