import * as React from 'react';
import * as glamorous from 'glamorous';
import { connect } from 'react-redux';
import {
  logoCardWrapper,
  logoCardImage,
  logoWrapper,
  verticalHelper,
} from './LogoGarden-styles';

const { Component, PropTypes } = React;
const {
  Div,
  Img,
  Span,
  A,
} = glamorous;

export interface ILogoCardProps {
  title: string;
  image: string;
  imageAlt?: string;
  image2x?: string;
  link: string;
}

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
});

export class LogoCardClass extends Component<ILogoCardProps, void> {
  static propTypes = {
    title: PropTypes.string,
    image: PropTypes.string,
    image2x: PropTypes.string,
    link: PropTypes.string,
  };

  static defaultProps = {
    imageAlt: '',
  };

  generateLogoWrapper(image, image2x, imageAlt) {
    return (
      <Div css={logoWrapper()}>
        <Span css={verticalHelper()}/>
        <Img
          srcSet={`${image ? `${image} 1x, ` : ''}${image2x ? `${image2x} 2x` : ''}`}
          src={image}
          alt={imageAlt}
          css={logoCardImage()}
        />
      </Div>
    );
  }

  render() {
    const {
      title,
      image,
      image2x,
      link,
      imageAlt,
    } = this.props;
    return (
      <Div css={logoCardWrapper()}>
      {link ? (
        <A href={link} target="_blank" title={title}>
          {this.generateLogoWrapper(image, image2x, imageAlt)}
        </A>
      ) : (
        this.generateLogoWrapper(image, image2x, imageAlt)
      )}
      </Div>
    );
  }
}

export const LogoCard = connect(mapStateToProps)(LogoCardClass);



