import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as glamorous from 'glamorous';
import { Link } from 'react-router';
import { whereAmI } from 'utils/helpers';
import { css } from 'glamor';

const {A, Div} = glamorous;

import * as styles from './Button-styles';

export interface IButtonProps {
  color: string;
  isSmall?: boolean;
  outline?: boolean;
  filled?: boolean;
  css?: object;
  onClick?: Function;
  link?: string;
  children?: React.ReactNode;
  analyticsEvent?: Function;
  sectionType?: string;
}

export const ButtonPropTypes = {
  color: PropTypes.string.isRequired,
  isSmall: PropTypes.bool,
  outline: PropTypes.bool,
  filled: PropTypes.bool,
  css: PropTypes.object,
  to: PropTypes.string,
};

export class Button extends React.Component<IButtonProps, void> {

  static propTypes = ButtonPropTypes;

  onClick(evt) {
    if (this.props.analyticsEvent) {
      this.props.analyticsEvent({
        action: 'click',
        label: `${whereAmI()}-button-${this.props.sectionType && this.props.sectionType}-${this.props.link}-${this.props.children}`,
        category: `${whereAmI()}-button-${this.props.children}`,
      });
    }
    if (this.props.onClick) {
      this.props.onClick(evt);
    }
  }
  renderWithLink() {
    const externalLink = this.props.link ? this.props.link.indexOf('http') : -1;
    const external = externalLink !== -1;

    const buttonStyles = css(styles.buttonStyles(this.props));
    const propsCSS = css(this.props.css);

    return !external ? (
      <Link
        {...css(buttonStyles, propsCSS)}
        to={this.props.link}
        onClick={(evt) => this.onClick(evt)}>
        { this.props.children }
      </Link>
    ) : (
      <A
        href={this.props.link}
        rel="external"
        target="_blank"
        css={[styles.buttonStyles(this.props), this.props.css]}
        className={ this.props.outline ? 'outline-button' : 'button' }
        onClick={(evt) => this.onClick(evt)}>
        { this.props.children }
      </A>
    );
  }
  render() {
    const { Button } = glamorous;

    return (!this.props.link) ?
          <glamorous.Button
            css={[styles.buttonStyles(this.props), this.props.css]}
            className={ this.props.outline ? 'outline-button' : 'button' }
            onClick={(evt) => this.onClick(evt)}>
            { this.props.children }
          </glamorous.Button>
        : <Div css={styles.buttonWrapper}> {this.renderWithLink()} </Div>;
  }
}



